@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Raleway:300, 300i, 400, 400i);
@import url(https://fonts.googleapis.com/css2?family=Josefin+Sans:wght@200&display=swap);
/** @format */

* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  font-family: "Josefin Sans", "Arial Narrow", "Arial", "sans-serif";
}

.header {
  width: 100%;
  line-height: 60px;
  background-color: #f5ba13;
  display: flex;
  align-items: center;
  padding-left: 50px;
  color: white;
  box-shadow: 5px 3px 15px -3px rgba(0, 0, 0, 0.5);
}
.main_note {
  width: 500px;
  padding: 15px 10px 5px 10px;
  margin: 20px auto;
}
.my_notes h1 {
  color: #34495e;
}
.my_notes p {
  color: grey;
}
form {
  min-height: 50%;
  color: #ecf0f1;
  padding: 15px 10px 5px 10px;
  box-shadow: 5px 3px 12px -5px rgba(0, 0, 0, 0.4);
  border-radius: 10px;
  position: relative;
}
.input {
  width: 100%;
  line-height: 30px;
  background: transparent;
  border: none;
  outline: none;
  font-size: 1.5rem;
  margin: 10px 0;
  font-weight: bold;
}
textarea {
  width: 100%;
  line-height: 30px;
  background: transparent;
  border: none;
  outline: none;
  font-size: 1rem;
  margin: 10px 0;
}
.MuiButton-root {
  width: 20px;
  height: 50px;
  position: absolute;
  bottom: -30px;
  left: 400px;
  cursor: pointer;
  background-color: #fff !important;
  color: #f4b400 !important;
  box-shadow: 0px 5px 12px -3px rgba(0, 0, 0, 0.3);
  border-radius: 50% !important;
}
.MuiButton-root .plus-sign {
  font-size: 45px !important;
  font-weight: 900;
  cursor: pointer;
  z-index: 222;
  background: transparent;
}

.MuiButton-root:hover {
  color: #fff !important;
  background: #f4b400 !important;
}
.MuiButton-root:hover .plus-sign {
  background: transparent;
}
.note {
  background: #fff;
  border-radius: 7px;
  box-shadow: 0 2px 5px #ccc;
  padding: 10px;
  width: 240px;
  margin: 16px;
  float: left;
}
.note h1 {
  font-size: 1.1em;
  margin-bottom: 6px;
}
.note p {
  font-size: 1.1rem;
  margin-bottom: 10px;
  white-space: pre-wrap;
  word-wrap: break-word;
}
.note .btn {
  position: relative;
  float: right;
  margin-right: 10px;
  color: #f4b400;
  border: none;
  width: 50px;
  height: 50px;
  background: transparent;
  cursor: pointer;
  outline: none;
  box-shadow: 3px 3px 15px -10px rgba(0, 0, 0, 0.5);
  border-radius: 50%;
}
.deleteIcon {
  width: 0.7em;
  height: 0.7em;
}
.note .btn:hover {
  background-color: #f5ba13;
}
.note .btn:hover .deleteIcon {
  color: #fff;
}
footer {
  width: 100%;
  position: absolute;
  bottom: 0;
  justify-content: center;
  align-items: center;
}
p.copyright {
  position: absolute;
  width: 100%;
  color: black;
  line-height: 40px;
  font-size: 1em;
  text-align: center;
  bottom: 0;
}

